.app-container {
  background-color: none;
  font-family: 'Noto Sans JP', sans-serif;
  color: #777777;
  background-image: url(/PatrickBrown/static/media/background.27866270.jpg);
  background-color: none;
  height: 100vh;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  margin-top: auto;
  clear: both;
  overflow: scroll;
}

::-webkit-scrollbar {
  width: 0;
  background: transparent;
}
.page-title {
  width: 100%;
  background: none;
  color: white;
  margin-top: 10px;
  display: flex;
  flex-direction: column;
  text-align: center;
  vertical-align: middle;
}

.about {}
* {
  font-family: "Montserrat", sans-serif;
}

.card-container {
  width: 100vw;
  margin: auto;
  padding: 10px;
  display: grid;
  justify-content: space-around;
  grid-template-columns: 31% 31% 31%;
  background-color: transparent;
  /* padding: 10px; */
}

.card-box {
  display: flex;
  background-image: linear-gradient(215deg,
      #fc7f4e,
      #e94390,
      #ff0164,
      #be0254,
      #5546cb,
      #3c91c2,
      #14e0f3);
  padding: 8.5px;
  margin-bottom: 50px;
  border-radius: 5px;
}

.card {
  background-color: #14e0f3;
  padding: 10px;
  width: 95%;
  /* height: 100%; */
  margin: 8px auto;
  border-radius: 5px;
  box-shadow: -5px 7px rgba(6, 64, 70, 0.27);
}

.project-img {
  display: flex;
  margin: auto;
  max-width: 90%;
}

.card-body {
  display: block;
}

.card-title {
  color: white;
  text-align: center;
}

.card-text {
  color: white;
  text-align: center;
  font-size: 16px;
  line-height: 30px;
}

.btn-link {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100%;
  width: 100%;
  color: white;
}

.btn-link:hover {
  color: #ff0164;
}

.project-btn {
  height: 100px;
  list-style: none;
  cursor: pointer;
  background-color: #ff0164;
  font-weight: bold;
  font-size: 16px;
  /* padding: 10px 5px; */
  height: 35px;
  border: none;
  border-radius: 5px;
}

.project-btn:hover {
  border: 2px solid #ff0164;
  background-color: #14e0f3;
  border-radius: 4px;
  transition: all .33s ease-in;
}

.right-btn {
  float: right;
  width: 100px;

}

.left-btn {
  width: 60px;

  float: left;
}

.card-footer {
  padding-top: 10px;
  vertical-align: middle;
}

hr {
  background-color: #ff0164;
  color: #ff0164;
  border-style: solid;
  border-width: 2px;
  border-radius: 2px;
  margin: 0.5em auto;
}

@media screen and (max-width: 960px) {
  .card-container {
    width: 100vw;
    margin: auto;
    display: grid;
    grid-template-columns: 47% 47%;
    background-color: transparent;
    /* padding: 10px; */
  }
}

@media screen and (max-width: 640px) {
  .card-container {
    justify-content: center;
    width: 100vw;
    margin: auto;
    display: grid;
    grid-template-columns: 95%;
    background-color: transparent;
    /* padding: 10px; */
  }
}
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: "Montserrat", sans-serif;
}

a {
  text-decoration: none;
}

.navbar {
  background-image: linear-gradient(to right,
      #fc7f4e,
      #e94390,
      #ff0164,
      #be0254,
      #740046,
      #530237,
      #460067,
      #5f077f,
      #5546cb,
      #3c91c2,
      #14e0f3);
  height: 80px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 1.2rem;
  border-bottom: 2px solid #310042;
}

.navbar-logo {
  color: white;
  justify-self: start;
  cursor: pointer;
  padding: 0.5rem 1rem;
  font-size: 24px;
  font-weight: bold;
}


.nav-menu {
  display: grid;
  grid-template-columns: repeat(5, auto);
  list-style: none;
  text-align: center;
  width: 70vw;
  justify-content: end;
  margin-left: 2rem;
}

.nav-link {
  font-weight: bold;
  color: white;
  text-decoration: none;
  padding: 0.5rem 1rem;
}

.navbar-logo:hover {
  background-color: #5546cb;
  color: #ff0164;
  border-radius: 4px;
  transition: all 2s cubic-bezier(0.075, 0.82, 0.165, 1);
}

.nav-link:hover {
  background-color: #e94390;
  /* color: #14e0f3; */
  border-radius: 4px;
  transition: all 2s cubic-bezier(0.075, 0.82, 0.165, 1);
}

.fa-bars {
  color: white;
}

.menu-icon {
  display: none;
}

@media screen and (max-width: 960px) {
  .navbar {
    position: relative;
  }

  .nav-menu {
    display: flex;
    flex-direction: column;
    width: 40%;
    height: auto;
    position: absolute;
    top: 5rem;
    right: -50%;
    opacity: 1;
    transition: all 0.5s ease;
    border-radius: 0 0 0 10px;
  }

  .nav-menu.active {
    /* background-color: #3c91c2; */
    background-image: linear-gradient(to right,
        #fc7f4e,
        #e94390,
        #ff0164,
        /* #be0254, */
        /* #740046, */
        /* #3b0026, */
        /* #310042, */
        /* #5f077f, */
        #5546cb,
        #3c91c2,
        #14e0f3);
    right: 0;
    opacity: 1;
    transition: all 0.5s ease;
    z-index: 1;
    padding-left: 0;
  }

  .nav-link {
    font-weight: bold;
    text-align: center;
    padding: 2rem 0;
    width: 100%;
    display: table;
  }

  .nav-link:hover {
    background-image: linear-gradient(to right,
        #14e0f3,
        #5546cb,
        #5546cb,
        #5546cb,
        #14e0f3);
    color: #ff0164;
    border-radius: 0;
  }

  .last-link:hover {
    border-radius: 0 0 0 10px;
  }

  .navbar-logo {
    position: absolute;
    top: 0;
    left: 0;
    transform: translate(15%, 37%);
    padding: 0.5rem 1rem;

  }

  /* color: white;
  justify-self: start;
  cursor: pointer;
  padding: 0.5rem 1rem;
  font-size: 24px;
  font-weight: bold; */
  .menu-icon {
    display: block;
    position: absolute;
    top: 0;
    right: 0;
    transform: translate(-100%, 60%);
    font-size: 1.8rem;
    cursor: pointer;
  }

  .fa-times {
    color: white;
    font-size: 2rem;
  }
}
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

